.coaches-card {
  position: relative;
  display: inline-block;

  &::-webkit-scrollbar {
    display: none;
  }

  &:hover &__list {
    top: 0;
    opacity: 1;
  }

  &:hover &__title {
    margin: 0;
    z-index: 3;
    text-align: center;
  }

  &:hover &__item,
  &:hover &__spec {
    opacity: 1;
  }

  &:hover &__spec {
    text-align: center;
    height: auto;
    width: auto;
  }

  &__wrapper {
    height: 100%;
    max-height: 294px;
    margin: 0;
  }

  &__content {
    padding: 51px 30px 23px;

    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    overflow: auto;

    display: flex;
    flex-direction: column;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__list {
    transition: 0.2s;
    margin: 0;
    padding: 58px 57px;
    padding-left: 42px;
    padding-top: 137px;
    max-width: 260px;
    width: 100%;

    height: 100%;

    position: absolute;
    top: -294px;
    left: 0;

    z-index: 2;

    display: flex;
    flex-direction: column;

    background-color: $color-coaches-card-bg;
    color: $color-default-white;

    @include vp-767 {
      padding: 48px 40px;
      padding-left: 29px;
    }
  }

  &__item {
    margin-bottom: 7px;
    padding-left: 16px;

    z-index: 2;

    list-style: none;
    opacity: 0;

    position: relative;

    &::before {
      width: 6px;
      height: 6px;

      content: "";
      border-radius: 50%;

      background-color: $color-default-white;

      position: absolute;
      z-index: 2;
      left: 4px;
      top: 7px;
    }
  }

  &__title {
    margin: 0;
    margin-top: auto;

    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
    color: $color-default-white;

    // @include vp-767 {
    //   top: 896%;
    // }
  }

  &__spec {
    height: 0;
    width: 0;

    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: $color-spindle;

    opacity: 0;

    z-index: 3;

    display: block;
    text-align: center;
  }

  &__text {
    margin: 0;

    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
  }
}
